import React, { useState, useRef } from 'react';
import { Button } from 'primereact/button';
import { Galleria } from 'primereact/galleria';
import { classNames } from 'primereact/utils';
import './ImageGalleria.css';

/**
 * Advanced Galleria component for showing images with fullscreen and thumbnail capabilities
 * @param {Array} images - Array of base64 image strings
 * @param {String} title - Title of the post/recipe
 */
export default function ImageGalleria({ images, title }) {
    const [activeIndex, setActiveIndex] = useState(0);
    const [showThumbnails, setShowThumbnails] = useState(false);
    const [isAutoPlayActive, setAutoPlayActive] = useState(false);
    const [isFullScreen, setFullScreen] = useState(false);
    
    const galleria = useRef(null);

    // Format the images for Galleria
    const formattedImages = images.map((image, index) => {
        return {
            itemImageSrc: `data:image/png;base64,${image}`,
            thumbnailImageSrc: `data:image/png;base64,${image}`,
            alt: `${title} - Image ${index + 1}`,
            title: `${title} (${index + 1}/${images.length})`
        };
    });

    const responsiveOptions = [
        {
            breakpoint: '1024px',
            numVisible: 5
        },
        {
            breakpoint: '960px',
            numVisible: 4
        },
        {
            breakpoint: '768px',
            numVisible: 3
        },
        {
            breakpoint: '560px',
            numVisible: 1
        }
    ];

    const onItemChange = (event) => {
        setActiveIndex(event.index);
    };

    const toggleFullScreen = () => {
        if (isFullScreen) {
            closeFullScreen();
        } else {
            openFullScreen();
        }
    };

    const onFullScreenChange = () => {
        setFullScreen(prevState => !prevState);
    };

    const openFullScreen = () => {
        let elem = document.querySelector('.custom-galleria');
        if (elem.requestFullscreen) {
            elem.requestFullscreen();
        } else if (elem.mozRequestFullScreen) { /* Firefox */
            elem.mozRequestFullScreen();
        } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari & Opera */
            elem.webkitRequestFullscreen();
        } else if (elem.msRequestFullscreen) { /* IE/Edge */
            elem.msRequestFullscreen();
        }
    };

    const closeFullScreen = () => {
        if (document.exitFullscreen) {
            document.exitFullscreen();
        } else if (document.mozCancelFullScreen) {
            document.mozCancelFullScreen();
        } else if (document.webkitExitFullscreen) {
            document.webkitExitFullscreen();
        } else if (document.msExitFullscreen) {
            document.msExitFullscreen();
        }
    };

    React.useEffect(() => {
        const bindDocumentListeners = () => {
            document.addEventListener("fullscreenchange", onFullScreenChange);
            document.addEventListener("mozfullscreenchange", onFullScreenChange);
            document.addEventListener("webkitfullscreenchange", onFullScreenChange);
            document.addEventListener("msfullscreenchange", onFullScreenChange);
        };

        const unbindDocumentListeners = () => {
            document.removeEventListener("fullscreenchange", onFullScreenChange);
            document.removeEventListener("mozfullscreenchange", onFullScreenChange);
            document.removeEventListener("webkitfullscreenchange", onFullScreenChange);
            document.removeEventListener("msfullscreenchange", onFullScreenChange);
        };

        bindDocumentListeners();
        return () => unbindDocumentListeners();
    }, []);
    
    React.useEffect(() => {
        if (galleria.current) {
            setAutoPlayActive(galleria.current.isAutoPlayActive());
        }
    }, [isAutoPlayActive]);

    const thumbnailTemplate = (item) => {
        return (
            <div className="grid grid-nogutter justify-content-center">
                <img src={item.thumbnailImageSrc} alt={item.alt} style={{ display: 'block' }} />
            </div>
        );
    };    const itemTemplate = (item) => {
        if (isFullScreen) {
            return <img src={item.itemImageSrc} alt={item.alt} style={{ maxWidth: '100%', maxHeight: '100vh', display: 'block' }} />;
        }

        return <img 
            src={item.itemImageSrc} 
            alt={item.alt} 
            style={{ 
                width: 'auto', 
                height: 'auto', 
                maxWidth: '85%',
                maxHeight: '300px',
                display: 'block', 
                objectFit: 'contain',
                margin: '0 auto',
                borderRadius: '4px'
            }} 
        />;
    };

    const renderFooter = () => {
        let autoPlayClassName = classNames('pi', {
            'pi-play': !isAutoPlayActive,
            'pi-pause': isAutoPlayActive
        });

        let fullScreenClassName = classNames('pi', {
            'pi-window-maximize': !isFullScreen,
            'pi-window-minimize': isFullScreen
        });

        return (
            <div className="custom-galleria-footer">
                <Button 
                    icon="pi pi-list" 
                    onClick={() => setShowThumbnails(prevState => !prevState)} 
                    className="p-button-rounded p-button-text" 
                />
                <Button 
                    icon={autoPlayClassName} 
                    onClick={() => {
                        if (!isAutoPlayActive) {
                            galleria.current.startSlideShow();
                            setAutoPlayActive(true);
                        }
                        else {
                            galleria.current.stopSlideShow();
                            setAutoPlayActive(false);
                        }
                    }} 
                    className="p-button-rounded p-button-text" 
                />
                {
                    formattedImages && (
                        <span className="title-container">
                            <span>{activeIndex + 1}/{formattedImages.length}</span>
                            <span className="title">{title}</span>
                        </span>
                    )
                }
                <Button 
                    icon={fullScreenClassName} 
                    onClick={() => toggleFullScreen()} 
                    className="p-button-rounded p-button-text fullscreen-button" 
                />
            </div>
        );
    };

    const footer = renderFooter();
    const galleriaClassName = classNames('custom-galleria', {
        'fullscreen': isFullScreen
    });

    return (
        <div className="galleria-container">            <Galleria 
                ref={galleria} 
                value={formattedImages} 
                activeIndex={activeIndex} 
                onItemChange={onItemChange}
                showThumbnails={showThumbnails} 
                showItemNavigators 
                showItemNavigatorsOnHover
                numVisible={5} 
                circular 
                autoPlay 
                transitionInterval={3000} 
                responsiveOptions={responsiveOptions}
                item={itemTemplate} 
                thumbnail={thumbnailTemplate} 
                footer={footer}
                className={galleriaClassName}
                style={{ 
                    width: '100%', 
                    maxWidth: '800px',
                    margin: '0 auto',
                    height: '100%', 
                    minHeight: '380px' 
                }}
            />
        </div>
    );
}
